<template>
  <div class="Myaddress">
    <van-nav-bar title="收货地址" left-text="返回" left-arrow @click-left="goback" />
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      default-tag-text="默认"
      :disabled-list="disabledList"
      disabled-text="以下地址超出配送范围"
      @add="AddorEdit"
      @edit="AddorEdit"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      chosenAddressId: "1", //控制默认
      //地址列表
      list: [],
      disabledList: [
        {
          id: "3",
          name: "王五",
          tel: "1320000000",
          address: "浙江省杭州市滨江区江南大道 15 号"
        }
      ]
    };
  },
  methods: {
    goback() {
      this.$router.go(-2);
    },
    //获取地址数据
    getAddress() {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 20000) {
            result.data.result.forEach(v => {
              let addresslist = {
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address: v.province + v.city + v.county + v.addressDetail, //地址
                isDefault: Boolean(v.isDefault)
              };
              //
              if (v.isDefault) {
                //选择默认地址chosenAddressId
                this.chosenAddressId = v.aid;
              }

              this.list.push(addresslist);
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //新增编辑共用 编辑需要带参数aid
    AddorEdit(address) {
      //Toast("新增地址");

      let e = { name: "Address" };
      if (address) {
        e.params = { aid: address.id };
      }
      this.$router.push(e); //{name:'Address',params:{aid}}
    }
  },
  created() {
    this.getAddress(); //加载完成就获取
  }
};
</script>

<style lang="less" scoped>
/deep/.van-nav-bar__text {
  color: #f0f0f0;
}
/deep/.van-nav-bar {
  background-color: rgb(249, 187, 50);
  color: #f0f0f0;
}
/deep/.van-nav-bar .van-icon {
  color: #f0f0f0;
}
/deep/.van-address-item .van-radio__icon--checked .van-icon {
  background-color: rgb(249, 187, 50);
  border-color: rgb(249, 187, 50);
}
.van-tag--danger {
  background-color: rgb(249, 187, 50);
}
.van-button--danger{
background-color: rgb(249, 187, 50);
border-color:  rgb(249, 187, 50);
}
</style>